.container {
    height: auto;
    max-height: min-content;
    max-width: 500px;
    padding: 24px 0;
    padding-right: 12px;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 24px;
    box-shadow: 0 0 20px 0 #000;
    position: relative;
}

.icon-check {
    padding-left: 16px;
    transition: color;
    transition-duration: 200ms;
    user-select: none;
    -webkit-user-select: none;
    font-size: 34px;
    font-weight: bold;
}

.icon-check:hover {
    color: #00f;
    cursor: pointer;
}

.icon-check__active {
    color: #00f;
}

.todoitem-text {
    height: auto;
    max-height: min-content;
    max-width: 310px;
    text-align: left;
    margin-right: auto;
    white-space: normal; /* Establece el comportamiento normal de espacio en blanco */
    word-wrap: break-word; /* Fuerza el wrap de palabras largas */
    overflow-x: hidden; /* Oculta el desbordamiento horizontal */
    overflow-y: auto; /* AÃ±ade una barra de desplazamiento vertical si es necesario */
}

.todo-item__completed {
    text-decoration: line-through;
}

.icon-close {
    padding-right: 16px;
    margin-bottom: auto;
    transition: color;
    transition-duration: 200ms;
    user-select: none;
    -webkit-user-select: none;
    font-size: 34px;
    margin-right: 20px;
    font-weight: bold;
    position: absolute;
    top: -19px;
    right: -30px;
}

.icon-close:hover {
    color: #f00;
    cursor: pointer;
}