.window {
    position: absolute;
    display: flex;
    height: auto;
    width: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
}

.window_closed {
    display: none;
}

.card {
    border: 2px solid black;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur((5px));
    padding: 6px 20px;
    border-radius: 8px;
    width: 80%;
    max-width: 500px;
}

.title {
    color: blue;
    font-size: 32px;
}

.inpt_create--todo {
    outline: none;
    border: none;
    padding: 24px 24px;
    font-size: 24px;
    border-radius: 8px;
    width: 70%;
    resize: vertical;
}

.inpt_create--todo:hover, .inpt_create--todo:focus, .inpt_create--todo:not(:placeholder-shown) {
    outline: 2px solid blue;
}

.inpt_create--todo::placeholder {
    text-align: center;
    font-size: 24px;
}

.btn_container {
    margin: 24px 0;
    display: flex;
    column-gap: 20px;
    justify-content: center;
}

.btn {
    border: none;
    font-size: 24px;
    padding: 6px 12px;
    cursor: pointer;
    border-radius: 8px;
    transition: background-color, color;
    transition-duration: 200ms;
    user-select: none;
    -webkit-user-select: none;    
}

.btn_close {
    background-color: #737373;
}

.btn_close:hover {
    color: white;
}

.btn_create {
    background-color: blue;
    color: white;
}

