.main_button {
    background-color: #00f;
    border-radius: 50%;
    width: 70px;
    height: 70px;
    border: none;
    font-size: 32px;
    text-align: center;
    cursor: pointer;
    transition-property: background-color;
    transition-duration: 100ms;
    box-shadow: 0 0 20px #020f68;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    position: fixed;
    bottom: 12px;
    right: 12px;
}

button:hover {
    background-color: #000448;
}

button:active {
    background-color: rgb(1, 0, 25);
}